body {
  display: block;
}


body .App {
  margin: 0;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #f6f6f6;
  font-family: Open Sans,Helvetica Neue,sans-serif;
  font-weight: 400;
  color: #333;
  -webkit-font-smoothing: antialiased;
  font-size: 14px; }

body .p-menu {
  padding: 0;
  background-color: #ffffff;
  border: 1px solid #c8c8c8;
  border-radius: 3px;
  width: 100%; }

.layout-topbar {
  background-color: #20272a;
  display: block;
  padding: 0;
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3); }

.layout-sidebar {
  position: fixed;
  left: 0;
  top: 70px;
  height: 100%;
  background-color: #fff;
  width: 13%;
  overflow: auto;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2); }

.layout-menu > button {
  width: 100%;
  height: 48px;
  display: block;
  padding: 0px 24px;
  color: #333333;
  background-color: transparent;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: left;
  margin: 0;
  border: none;
  border-top: solid 1px #e3e9ea;
  cursor: pointer; }
  .layout-menu > button:hover {
    background-color: #eeeeee; }
  .layout-menu > button:focus {
    z-index: 1;
    outline: 0 none;
    -webkit-transition: box-shadow .3s;
    transition: box-shadow .3s;
    box-shadow: inset 0 0 0 0.2em #00c4e8; }
  .layout-menu > button span {
    font-size: 16px;
    vertical-align: middle;
    margin-left: 8px;
    position: relative;
    top: -2px; }
  .layout-menu > button img {
    width: 20px;
    vertical-align: middle; }
    .layout-menu > button img.layout-menu-icon-active {
      display: none; }
    .layout-menu > button img.layout-menu-icon-inactive {
      display: inline; }
  .layout-menu > button.active-menuitem {
    background-color: #ffffff;
    color: #03c4e8; }
    .layout-menu > button.active-menuitem img.layout-menu-icon-active {
      display: inline; }
    .layout-menu > button.active-menuitem img.layout-menu-icon-inactive {
      display: none; }

.layout-content {
  margin-left: 13%;
